//  
@import "vars";
@import "fonts";
@import "mixins";
@import "settings";
:root{
    --gilroy: 'Gilroy', sans-serif; 
}
body {
    margin: 0;
    font-family: 'TT Norms', sans-serif;
    text-size-adjust: 100%;
    font-size: 16px;
    line-height: 1.5;
    font-stretch: 100%;
    font-weight: 400;
    min-width: 320px;
    background-color: #181818;
    color: rgba($color: #fff, $alpha: 0.5);
    letter-spacing: -0.02em;

}
.wrap{
    max-width: 1300px;
    width: 100%;
    margin: 0 auto;
    padding-right: 30px;
    position: relative;
    z-index: 2;
    padding-left: 30px;
}
@media(max-width: 1300px){
    .wrap{
        max-width: 1100px;
    }
}
@media(max-width: 1099px){
    .wrap{
        max-width: 992px;
    }
}
@media(max-width: 991px){
    .wrap{
        max-width: 768px;
    }
}
@media(max-width: 767px){
    .wrap{
        max-width: 575px;
    }
}
@media(max-width: 574px){
    .wrap{
        padding-right: 20px;
        padding-left: 20px;
    }
}
.text-center{
    text-align: center;
}
a{
    color: inherit;
}
.app{
    padding-top: 246px;
    overflow: hidden;
    max-width: 2800px;
    margin: 0 auto;
}
@media(max-width: 991px){
    .app{
        padding-top: 85px;
    }
}

.btn{
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    color: #fff;
    padding: 26px 25px;
    max-width: 306px;
    width: 100%;
    background: #3C4AD2;
    border: none;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    transition: 0.2s all;
    cursor: pointer;
}
.btn:hover{
    box-shadow: 0 0 20px rgba($color: #3C4AD2, $alpha: 0.5)
}
.btn-border{
    padding: 13px 71px 19px;
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    color: #fff;
    border: 1px solid #3C4AD2;
    transition: 0.3s all;
    background-color: transparent;
    cursor: pointer;
}
.btn-border:hover{
    background: #3C4AD2!important;
    cursor: pointer;
    color: white!important;
}
.btn_reviews_form:hover{
    transition: 0.3s;
    background: #3C4AD2!important;
    cursor: pointer;
    color: white!important;
}


.btn_modal_form{
    margin-bottom: 10px;
}
.btn_modal_form:hover{
    transition: 0.3s;
    cursor: pointer;
    box-shadow: 0 0 20px rgb(60 74 210 / 30%);
}
.btn_object_info_irp:hover{
    transition: 0.3s;
    cursor: pointer;
    box-shadow: 0 0 20px rgb(60 74 210 / 30%);
}
.btn_play_video_swiper_irp:hover{
    transition: 0.3s;
    cursor: pointer;
    box-shadow: 0 0 20px rgb(60 74 210 / 30%);
}
.btn_preview_residential_complex{
    transition: 0.3s;
    cursor: pointer;
    box-shadow: 0 0 20px rgb(60 74 210 / 30%);
}
.btn_price_box_right_bottom_info_catalog:hover{
    transition: 0.3s;
    background: #3C4AD2!important;
    cursor: pointer;
    color: white!important;
}
.btn_all_news_desk:hover{
    transition: 0.3s;
    background: #3C4AD2!important;
    box-shadow: 0 0 20px rgb(60 74 210 / 0%)!important;
    cursor: pointer;
    color: white!important;
}
.mini_btn_ipoteka_bottom_info_catalog:hover{
    color: white;
}
.company__media__video:hover{
    span{
        border: 1px solid white;
    }
}
.btn_back_news_detail:hover{
    .icon_btn_back_news_detail{
        transition: 0.3s;
        opacity: 1;
        cursor: pointer;
    }
    .text_btn_back_news_detail{
        transition: 0.3s;
        cursor: pointer;
        color: white;
    }
}
.btn-white{
    background-color: #fff;
    color: #3C4AD2;
}
.ipoteka_object_info_irp:hover{
    color: white;
}
.btn-white:hover{
    box-shadow: 0 0 20px rgba($color: #fff, $alpha: 0.5)
}
.swiper-button-next:focus, .swiper-button-prev:focus {
    outline: none;
    box-shadow: 0 0 0 0 rgb(0,0,0,0);
}
.swiper-button-next:active, .swiper-button-prev:active {
    outline: none;
    box-shadow: 0 0 0 0 rgb(0,0,0,0);
}
.swiper-button-next:hover, .swiper-button-prev:hover {
    outline: none;
    box-shadow: 0 0 0 0 rgb(0,0,0,0);
}
.pagination__arows a:focus, .swiper-button-prev-custom:focus, .swiper-button-next-custom:focus, .swiper-button-next_irp:focus, .swiper-button-prev_irp:focus {
    outline: none;
    box-shadow: 0 0 0 0 rgb(0,0,0,0);
}
.pagination__arows a:active, .swiper-button-prev-custom:active, .swiper-button-next-custom:active, .swiper-button-next_irp:active, .swiper-button-prev_irp:active {
    outline: none;
    box-shadow: 0 0 0 0 rgb(0,0,0,0);
}
.pagination__arows a:hover, .swiper-button-prev-custom:hover, .swiper-button-next-custom:hover, .swiper-button-next_irp:hover, .swiper-button-prev_irp:hover {
    outline: none;
    box-shadow: 0 0 0 0 rgb(0,0,0,0);
}

*{
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
@media(max-width: 767px){
    .btn{
        padding-top: 18px;
        padding-bottom: 18px;
        font-size: 13px;
    }
   
}
.input{
    padding: 0 27px;
    height: 53px;
    border: 1px solid rgba($color: #fff, $alpha: 0.4);
    background-color: transparent;
    width: 100%;
    font-size: 14px;
    line-height: 17px;
    color: #fff;
}
.input:focus{
    outline: navajowhite;
    border: 1px solid rgba($color: #fff, $alpha: 1);
}
.input::placeholder{
    color: rgba($color: #fff, $alpha: 0.5)
}
.d-flex{
    display: flex;
}
.flex-center{
    justify-content: center;
}

.plug_aggregator{
    background: #313131;
    height: 1000px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 64px;
    color: white;
}
.news__item__img{
    img{
        object-fit: cover;
    }
}
@media (max-width: 767px){
        
    .plug_aggregator{
        max-height: 90vh;
        font-size: 36px;
    }
}

