:root {
  --gilroy: "Gilroy", sans-serif;
}

body {
  margin: 0;
  font-family: "TT Norms", sans-serif;
  text-size-adjust: 100%;
  font-size: 16px;
  line-height: 1.5;
  font-stretch: 100%;
  font-weight: 400;
  min-width: 320px;
  background-color: #181818;
  color: rgba(255, 255, 255, 0.5);
