.swiper__action {
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 1;
  width: 100%;
}
.swiper__action .wrap {
  position: relative;
}
.swiper__action .swiper-button-next, .swiper__action .swiper-button-prev {
  right: 30px;
  left: auto;
  top: auto;
  bottom: 36px;
  height: 40px;
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translate(0);
}
.swiper__action .swiper-button-next:after, .swiper__action .swiper-button-prev:after {
  font-size: 15px;
  color: #fff;
}
.swiper__action .swiper-button-prev {
  right: 94px;
}
.swiper__action .swiper-button-prev:before {
  content: "";
  display: block;
  position: absolute;
  right: -12px;
  height: 34px;
  border-right: 1px solid rgba(255, 255, 255, 0.2);
