
.about_residential_complex{
    position: relative;

    .wrap{
        position: relative;
        z-index: 2;
    }
}
.about_residential_complex::after{
    content: "";
    position: absolute;
    display: block;
    right: 10%;
    height: 500px;
    width: 500px;
    opacity: 1;
    top: 50px;
    background: #3C4AD2;
    opacity: 0.2;
    filter: blur(254px);
}
.about_company_residential_complex{
    position: relative;

    .wrap{
        position: relative;
        z-index: 2;
    }
}
.about_company_residential_complex::after{
    content: "";
    position: absolute;
    display: block;
    right: 10%;
    height: 500px;
    width: 500px;
    opacity: 1;
    z-index: 1;
    top: 50px;
    background: #3C4AD2;
    opacity: 0.2;
    filter: blur(254px);
}

.preview_residential_complex{
    margin-top: -246px;
    height: 100vh;
    min-height: 500px;
    position: relative;

    .background_shape_preview_residential_complex{
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1;

        img{
            height: 100%;
            object-fit: cover;
        }
    }
    .background_shape_preview_residential_complex:after{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 2;
        background: url(/img/main/76.png)no-repeat center;
        background-position: right;
        background-size: cover
    }
    .background_shape_preview_residential_complex:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 2;
        opacity: 0.56;
        background: url(/img/main/86.png)no-repeat bottom;
        background-size: cover;
    }
    .wrap{
        height: 100%;
        position: relative;
        z-index: 3;
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        padding-bottom: 50px;
    }

    .title_h1{
        font-weight: 400;
        font-size: 48px;
        line-height: 99.52%;
        color: white;
    }

    .full_price{
        font-weight: 400;
        font-size: 20px;
        line-height: 99.52%;
        margin-top: 23px;
        color: white;

        span{
            font-size: 14px;
        }
    }

    .square_price{
        font-weight: 400;
        font-size: 16px;
        line-height: 1.5;
        margin-top: 7px;
    }

    .text_preview_residential_complex{
        color: rgb(255,255,255, 0.78);
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        max-width: 450px;
    }

    .btn_preview_residential_complex{
        width: 317px;
        height: 70px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #3C4AD2;
        color: white;
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
    }

}

.params_residential_complex{
    padding: 72px 0 18px;

    .wrap{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }

    .item_params_residential_complex{
        width: calc(33% - 15px);
        display: flex;
        align-items: center;
        margin-bottom: 58px;

        .icon_item_params_residential_complex{
            width: 93px;
            height: 93px;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            background: #2C2C2C;

            img{
                width: 47px;
                object-fit: cover;
            }
        }

        .info_item_params_residential_complex{
            margin-left: 28px;
            width: calc(100% - 121px);
        }

        .mini_tile_item_params_residential_complex{
            font-weight: 400;
            font-size: 16px;
            line-height: 1.5;
            color: rgb(255,255,255, 0.6);
        }
        
        .text_item_params_residential_complex{
            font-weight: 400;
            font-size: 37px;
            line-height: 1;
            margin-top: 5px;
            color: white;

            span{
                font-size: 30px;
            }
        }

    }
}

.about_residential_complex{
    padding-bottom: 120px;

    .row_top_about_residential_complex{
        display: flex;
        align-items: center;

        .title_about_residential_complex{
            min-height: 150px;
            display: flex;
            align-items: center;
            width: 400px;
            padding: 0 60px 0 0;
            background: #3C4AD2;  
            position: relative;
            font-weight: 400;
            font-size: 36px;
            color: white;
            line-height: 1;
            margin-right: 60px;
        }
        .title_about_residential_complex:before{
            content: '';
            position: absolute;
            top: 0;
            bottom: 0;
            right: 100%;
            width: 2000px;
            background: #3C4AD2;  
              
        }

        .tabs_about_residential_complex__nav{
            display: flex;
            align-items: center;

            li{     
                padding: 17px 0;
                font-weight: 400;
                font-size: 24px;
                line-height: 1;
                cursor: pointer;
                transition: 0.25s;
                color: rgb(255,255,255,0.5);
                border-bottom: 1px solid rgb(0,0,0,0);
                margin-right: 40px;
            }
            li:hover{     
                color: white;
            }
            li.active{     
                color: white;
                border-bottom: 1px solid rgb(255,255,255,1);
            }
        }

    }
    .tabs_about_residential_complex__content{
        display: none;
        padding-top: 66px;
        align-items: center;
        justify-content: space-between;
    }

    .tabs_about_residential_complex__content.active{
        display: flex;
    }

    .video_box_residential_complex{
        width: 450px;
        height: 450px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-size: cover;

        span{
            display: flex;
            align-items: center;
            justify-content: center;
            width: 101px;
            height: 101px;
            border-radius: 100%;
            border: 1px solid rgba(255, 255, 255, 0.5);
        }

    }

    .info_box_residential_complex{
        width: calc(100% - 525px);
        min-height: 450px;
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        justify-content: space-between;

        .text_residential_complex{
            max-width: 560px;
            font-weight: 400;
            font-size: 16px;
            line-height: 150%;
        }
    }
}

.catalog{
    .title_catalog{
        font-weight: 400;
        font-size: 48px;
        line-height: 1;
        color: white;
        margin-bottom: 46px;
    }

    .filtr_catalog{
        padding: 33px 25px;
        background: #3C4AD2;
        display: flex;
        align-items: center;
        justify-content: space-between;
        
        .title_filtr_catalog{
            font-weight: 500;
            font-size: 24px;
            line-height: 1;
            color: white;
        }
        .select-selected{
            color: rgb(255 255 255);
            padding: 17px 35px 17px 20px;
            border: 1px solid white;
        }
    
        .select-selected:before{
            content: "";
            width: 8px;
            height: 8px;
            display: block;
            position: absolute;
            right: 15px;
            top: calc(50% - 6px);
            transform: rotate(45deg);
            border-right: 1px solid;
            border-bottom: 1px solid;
            border-color: rgb(255 255 255);
        }
    
        .custom-select{
            width: calc(20% - 45px)
        }
    }

    .top_sort_catalog{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-top: 60px;
    }

    .items_box_catalog{
        padding-top: 40px;
    }

    .bottom_sort_catalog{
        padding-top: 40px;
        display: flex;
        border-top: 1px solid rgb(255,255,255, 0.6);
        align-items: center;
        justify-content: space-between;
    }

    .item_box_catalog{
        display: flex;
        justify-content: space-between;
        margin-bottom: 50px;

