            width: 400px;
            height: 245px;
            overflow: hidden;
        }
        .swiper{
            height: 100%;
        }
        .swiper-slide{
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            z-index: 1;
        }
        .photo_item_slide{
            object-fit: cover;
        }
        .swiper-slide:after{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: 2;
            background: url(/img/main/light67.png)no-repeat center;
            background-position: right;
            background-size: cover
        }
        .swiper-slide:before{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: 2;
            background: url(/img/main/light55.png)no-repeat bottom;
            background-size: cover;
        }
        .info_item_box_catalog{
            width: calc(100% - 450px);
        }

        .top_info_item_box_catalog{
            display: flex;
            justify-content: space-between;
        }

        .title_top_info_item_box_catalog{
