            margin-top: 23px;
            border: 1px solid #3C4AD2;
            width: 218px;
            height: 50px;
            font-weight: 500;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 15px;
            line-height: 18px;
            color: white;
        }
        .mini_btn_ipoteka_bottom_info_catalog{
            display: flex;
            align-items: center;
            justify-content: center;
            margin-top: 7px;
            width: 218px;
        }

    }
}
.gallery_residential_complex{
