    }
    .main-gallery__item__text{
        position: relative;
        z-index: 2;
        width: 100%;
        padding: 0 30px 30px;
        color: white;
        left: unset;
        bottom: unset;
        right: unset;
        opacity: 1;
    }
    .main-gallery__item__title{
        color: white;
        opacity: 1;
        position: relative;
        z-index: 2;
        margin-bottom: 8px;
        font-weight: 400;
        font-size: 24px;
        left: unset;
        bottom: unset;
        right: unset;
        line-height: 1;
        padding: 0 30px;
    }
    .h-title{
        margin-bottom: 50px;
    }
}

