.company_residential_complex{

    padding-bottom: 0;

    .h-title{
        
        padding-top: 80px;
        margin-top: 58px;
        border-top: 1px solid rgb(255,255,255, 0.6);
    }


    .company__numbers{
        display: flex;
        justify-content: space-between;
        padding-bottom: 80px;
        border-bottom: 1px solid rgb(255,255,255, 0.6);
    }

    .company__numbers__item{
        margin-right: 0;
        margin-bottom: 0;
    }
}

.faq{
    margin-top: 90px;

    .title_faq{
        padding: 57px 140px 57px 0;
        width: fit-content;
        position: relative;
        font-weight: 400;
        font-size: 36px;
        line-height: 1;
        color: white;
        background: #3C4AD2;
    }

    .title_faq:after{
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        right: 100%;
        width: 2000px;
        background: #3C4AD2;
    }
    
    .faq__list{
        margin-top: 70px;
    }

    .faq__row {
        padding-left: 40px;
        padding-right: 40px;
        padding-top: 34px;
        border: 1px solid rgba(255, 255, 255, 0.2);
        margin-bottom: 10px;
    }
    .faq__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-bottom: 40px;
    }

    .title_faq__header{
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        color: white;
        width: calc(100% - 85px);
    }

    .faq__btn {
        width: 65px;
        min-width: 65px;
        height: 65px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 100%;
        background: #2C2C2C;
        border: none;
        transition: 0.3s all;
        cursor: pointer;
    }
    .faq__btn:focus{
        outline: navajowhite;
    }
    .faq__btn.active svg{
        transform: rotate(180deg);
    }
    .faq__btn:hover{
        box-shadow: 0 0 10px rgba($color: #fff, $alpha: 0.1);
    }
    .faq__span {
        color: #FFFFFF;
        opacity: 0.5;
        margin-bottom: 7px;
    }
    .faq__body{
        color: #fff;
        font-size: 14px;
        padding-bottom: 40px;
        padding-top: 40px;
        border-top: 1px solid rgba(255, 255, 255, 0.5);
    }
}

.back_call_residential_complex{
    padding-bottom: 0;
}

